import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { Box, Button, CardActionArea, CardActions } from '@mui/material';
import StarIcon from '@mui/icons-material/Star';
import '../App.css'

export default function MultiActionAreaCard(props) {
    const { image } = props;
    const { title } = props;
    const { price } = props;
    const { rating } = props;
    const { sales,Onclick } = props;

    return (
        <Card sx={{  margin: 3, height: 450, padding: '5px',backgroundColor:'whitesmoke',boxShadow:' 0px 0px 2px #6c757d' }} onClick={Onclick}>
            <CardActionArea>
                <Box sx={{height:'290px', overflow:'hidden',textAlign:'center'}}>
                    <img src={image} width='80%'/>
                    </Box>
                <CardContent>
                    <Typography sx={{ fontSize: '16px', textAlign: 'justify', fontWeight: '600', color: '#4c4a4c' ,height:'34px', overflow:'hidden' }} variant="p" component="div">
                        {title}
                    </Typography>
                    <Typography sx={{ fontSize: '22px', textAlign: 'left', fontWeight: '600', padding: '10px 0px', color: '#3f3e40' }}>
                        {price}
                    </Typography>

                </CardContent>
            </CardActionArea>
            <CardActions>
                <Button variant='contained' size="small" color="success" sx={{ padding: '0px', borderRadius: '50px', paddingTop: '1px' }}>
                    {rating} <StarIcon fontSize='10px' sx={{ position: 'relative', bottom: '1px', paddingLeft: '2px' }} />
                </Button>
                <Typography sx={{ fontSize: '13px', color: 'grey', fontWeight: '500', paddingLeft: '5px' }}>{sales}</Typography>
            </CardActions>
        </Card>
    );
}