import { Box, FormControl, OutlinedInput, InputAdornment, Typography } from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';
import StoreIcon from '@mui/icons-material/Store';

function Navbar(props) {
    const { onSubmit, onChange } = props;

    return (
        <Box sx={{ width: '100%', height: '10vh', backgroundColor: '#063970', display: 'flex', justifyContent: 'space-around', boxSizing: 'border-box', margin: '0', padding: '0' }}>
            <Box sx={{ display: 'flex', color: 'whitesmoke' }}>
                <StoreIcon fontSize='large' sx={{ position: 'relative', top: '15px' }} />
                <Typography sx={{ fontSize: '17px', fontWeight: '700', position: 'relative', top: '22px' }}>MEGA STORE</Typography>
            </Box>
            <form onSubmit={onSubmit}>
                <FormControl sx={{ my: 1.5, width: '50vw' }} size='small' variant="outlined">
                    <OutlinedInput sx={{ backgroundColor: 'white',textTransform:'capitalize' }}
                        onChange={onChange}
                        placeholder="What are you looking for ?"
                        endAdornment={<InputAdornment position="end"><SearchIcon /></InputAdornment>}
                    />
                </FormControl>
            </form>

        </Box>
    )
}

export default Navbar;