import { HashRouter, Route, Routes } from "react-router-dom";
import Home from "../screens/Home";
import Products from "../screens/Product";





function Router(){


    return(
        <HashRouter>
            <Routes>
                <Route path="Products" element={<Products/>}/>
                <Route path="/" element={<Home/>}/>
            </Routes>
        </HashRouter>
    )
}

export default Router;