
import { Box, Grid } from '@mui/material';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../App.css';
import Card from '../components/Card';
import Loader from '../components/Loader';
import Navbar from '../components/Navbar';

function Home() {
    const [isLoading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    const [searchVal, setSearchVal] = useState('');
    const Search = (event) =>{
        setSearchVal(event.target.value);
        
    }
    
    const navigate = useNavigate();
    const movetoProducts = (e) => {
        navigate('Products',{state:e})
    }
    const renderData = () => {
        axios.get('https://fakestoreapi.com/products/')
            .then((arry) => {
                setData(arry.data);
                setLoading(false);
            })
            .catch((err) => {
                console.log(err);
                setLoading(false);
            })
    }
    useEffect(() => {
        renderData()
    }, []);
    return (
        
        <div>
            <Navbar onChange={Search}/>
            {
                isLoading ? <Box >
                    <Loader/>
                </Box> : <Grid container>
                {data.filter((x,i)=>x.title.toLowerCase().includes(searchVal.toLowerCase())|| x.category.toLowerCase().includes(searchVal.toLowerCase())).map((x, i)=> {
                        return (
                            <Grid className="cards p-1" key={i} item md={4} lg={3} sm={6} xs={12}>
                                <Card Onclick={()=> movetoProducts(x)} image={x.image} title={x.title} price={'$' + x.price} rating={x.rating.rate} sales={'(' + x.rating.count + ')'}></Card>
                            </Grid>
                        )
                    })}
                </Grid>
                    
            }
        </div>

    );
}

export default Home;