
import { useLocation } from 'react-router-dom';
import {Box,Button,Grid, Rating, Typography} from '@mui/material';

import '../App.css';


function Products() {
    const Location = useLocation();
    const Product = Location.state;
    console.log(Product)
    
    return(
      <Box sx={{display:'flex',height:'100vh',backgroundColor:'#d9d9d9'}}>
        <Box className='Image' sx={{width:'48vw',padding:'15px',justifyContent:'center',justifyItems:'center',display:'flex'}}>
            <img width={550} src={Product.image}></img>
        </Box>
        <Box className='Details' sx={{width:'48vw',padding:'25px 35px 15px 15px',textAlign:'justify',color:'#0b090a'}}>
            <Typography sx={{fontWeight:'600'}} variant='h6'>CATEGORY: {Product.category.toUpperCase()}</Typography>
            <Typography sx={{fontWeight:'700',marginY:'35px',height:'165px',overflow:'hidden',textAlign:'left'}} variant='h3'>{Product.title}</Typography>
            <Typography sx={{fontWeight:'600'}} variant='h6'>About Product:</Typography>
            <Typography sx={{fontWeight:'500',color:'#343a40'}} variant='p'>{Product.description}</Typography>
            <Typography sx={{fontWeight:'700',marginY:'35px'}} variant='h3'>Price: ${Product.price}</Typography>
            <Typography sx={{fontWeight:'600',fontSize:'20px',marginRight:'70px',textAlign:'left'}} variant='p'>RATING: <Rating size='large' sx={{position:'relative',top:'5px'}} name="read-only" value={Product.rating.rate} readOnly /></Typography>
            <Button variant='contained' color='success' sx={{width:'200px',position:'relative',bottom:'5px',fontWeight:'600'}}>Buy</Button>
        </Box>
      </Box>
    )
}


export default Products;